import React from "react";
import { Text, Box, HStack } from "@chakra-ui/core";
import SkillItem from "./SkillItem";

const Skills = () => {
  return (
    <>
      <Box>
        <Text textStyle={`h3`} my="20px">
          Skills
        </Text>
        <HStack>
          <SkillItem skill="Frontend" />
          <SkillItem skill="Javascript" />
          <SkillItem skill="PHP" />
          <SkillItem skill="CSS" />
          <SkillItem skill="NodeJS" />
          <SkillItem skill="NextJS" />
          <SkillItem skill="React" />
          <SkillItem skill="Git" />
        </HStack>
      </Box>
    </>
  );
};

export default Skills;
