import React from "react";
import { Box, Text, Link, Icon } from "@chakra-ui/core";
import { RiExternalLinkFill } from "react-icons/ri";

const ProjectItem = ({ project }) => {
  return (
    <Box
      position="relative"
      maxW="sm"
      borderRadius="lg"
      overflow="hidden"
      borderWidth="1px"
      mb={7}
      p={5}
    >
      <Text textStyle={`p`} mb="10px">
        {project.title}
      </Text>
      <Text my={3}>{project.description}</Text>
      <Link href={project.link} target="__blank">
        <Icon as={RiExternalLinkFill} />
      </Link>
    </Box>
  );
};

export default ProjectItem;
