import React from "react";
import { Text, Box } from "@chakra-ui/core";
import Socials from "../components/Socials";
import Image from "../components/Image";
import Skills from "../components/Skills";

const index = () => {
  return (
    <>
      <Text textStyle={`h1`}>Hi,There !</Text>
      <Image />
      <Box my="40px">
        <Text textStyle={`h3`} mb="10px">
          Hello! I'm Robert Ngabo, a young passionate developer from Rwanda.
        </Text>
        <Text textStyle={`p`} mb="10px">
          I have 2 years of hands-on experience efficiently software
          development, creative and fast websites and applications using cutting
          edge modern technologies?
        </Text>
        <Text textStyle={`p`} mb="20px">
          I am always looking for challenging opportunities
        </Text>

        <Socials />
        <Skills />
      </Box>
    </>
  );
};

export default index;
