import React from "react";
import { HStack, Link, Icon } from "@chakra-ui/core";
import { RiTwitterLine, RiLinkedinBoxLine, RiGithubLine } from "react-icons/ri";

const Socials = () => {
  return (
    <HStack my="50px">
      <Link href="https://twitter.com/robert_ngabo" target="__blank">
        <Icon as={RiTwitterLine} boxSize="40px" />
      </Link>
      <Link
        href="https://www.linkedin.com/in/robert-ngabo-63118b169/"
        target="__blank"
      >
        <Icon as={RiLinkedinBoxLine} boxSize="40px" />
      </Link>
      <Link href="https://github.com/ngaboindev" target="__blank">
        <Icon as={RiGithubLine} boxSize="40px" />
      </Link>
    </HStack>
  );
};

export default Socials;
