import React from "react";
import { RiMoonLine, RiSunLine } from "react-icons/ri";
import { Link } from "react-router-dom";
import {
  Box,
  Flex,
  Text,
  useColorMode,
  useColorModeValue,
  IconButton,
  Tooltip,
  Icon,
} from "@chakra-ui/core";

const NavLink = ({ children, ...props }) => (
  <Link style={{ padding: "0 10px" }} {...props}>
    {children}
  </Link>
);

const AppBar = () => {
  const { toggleColorMode } = useColorMode();
  const bgColor = useColorModeValue("rgb(236, 232, 232)", "gray.700");
  const iconSwitch = useColorModeValue(RiMoonLine, RiSunLine);

  return (
    <>
      <Flex
        w="100%"
        position="fixed"
        style={{ top: "0", zIndex: "1000" }}
        px={{
          base: "10px",
          md: "15px",
          xl: "290px",
        }}
        bg={bgColor}
        py={2}
        justifyContent="space-between"
        alignItems="center"
      >
        <Flex flexDirection="row" justifyContent="center" alignItems="center">
          <Link to="/" d="inline-block">
            <Text textStyle={`h2`}>RN</Text>
          </Link>
        </Flex>

        <Box>
          <Flex d="inline">
            <NavLink to="/projects">
              <Text textStyle={`p`} d="inline">
                Projects
              </Text>
            </NavLink>
            <NavLink to="/resume">
              <Text d="inline" textStyle={`p`}>
                Resume
              </Text>
            </NavLink>
          </Flex>

          <Tooltip label="Toggle Dark mode" placement="bottom">
            <IconButton
              style={{ borderRadius: "50%" }}
              onClick={toggleColorMode}
              variant="ghost"
            >
              <Icon as={iconSwitch} boxSize={6} />
            </IconButton>
          </Tooltip>
        </Box>
      </Flex>
    </>
  );
};

export default AppBar;
