import React from "react";
import { Text, Box } from "@chakra-ui/core";

const SkillItem = ({ skill }) => {
  return (
    <Box
      position="relative"
      maxW="sm"
      borderRadius="lg"
      overflow="hidden"
      borderWidth="1px"
      mb={7}
      p={2}
    >
      <Text textStyle={`p`}>{skill}</Text>
    </Box>
  );
};

export default SkillItem;
