import React from "react";
import { Grid } from "@chakra-ui/core";
import ProjectItem from "./ProjectItem";

const Project = () => {
  return (
    <Grid
      templateColumns={{
        base: "repeat(1, 1fr)",
        md: "repeat(2, 1fr)",
        xl: "repeat(2, 1fr)",
      }}
      gap={6}
      my="30px"
    >
      <ProjectItem
        project={{
          title: "Umva Live",
          description: "Simple Podcast Directory app using listen-notes api",
          link: "http://umva.live/",
        }}
      />
    </Grid>
  );
};

export default Project;
