import { extendTheme } from "@chakra-ui/core";

// 2. Define the new text styles
const theme = extendTheme({
  textStyles: {
    h1: {
      fontSize: ["20px", "30px", "40px"],
      fontWeight: "900",
      fontFamily: "Inter",
      lineHeight: "110%",
    },
    h2: {
      fontSize: ["17px", "20px", "25px"],
      fontWeight: "700",
      fontFamily: "Inter",
      lineHeight: "120%",
      letterSpacing: "-1%",
    },
    h3: {
      fontSize: ["17px", "20px", "18px"],
      fontWeight: "800",
      fontFamily: "Inter",
      lineHeight: "120%",
      letterSpacing: "-1%",
    },
    p: {
      fontSize: ["13px", "14px", "15px"],
      fontWeight: "500",
      fontFamily: "Inter",
      lineHeight: "170%",
      letterSpacing: "-1%",
    },
  },
});

export default theme;
